import { Component } from "react";
import { CardList } from "./components/card-list/card-list.component";
import { SearchInput } from "./components/search-input/search-input.component";

import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      artists: [],
      searchInput: "",
    };
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.cypress.io/users")
      .then((response) => response.json())
      .then((artists) => this.setState({ artists: artists }));
  }

  handleChange = (e) => this.setState({ searchInput: e.target.value });

  render() {
    const { artists, searchInput } = this.state;

    const filteredArtists = artists.filter((item) =>
      item.name.toLowerCase().includes(searchInput.toLowerCase())
    );

    return (
      <div className="App">
        <h1 className="app-name">Artist Searcher</h1>
        <SearchInput
          placeholder={"Search Artists"}
          handleChange={this.handleChange}
        />
        <CardList artists={filteredArtists}></CardList>
      </div>
    );
  }
}

export default App;
